<template>



	<form @submit="formSubmit" @reset="formReset">
		<view class="white_bac">



			<view class="linestyle">
				<view class="linestyle_left">标题:</view>
				<view class="linestyle_right">
					<input class="linestyle_right_right" style="width: 100%;" name="title" placeholder=" 20字以内" />
				</view>

			</view>




			<view class="linestyle">
				<view class="linestyle_left">
					类别:
				</view>
				<view class="linestyle_right">
					<picker class=" border-style-blue icontext blue" name="picker" @change="bindPickerChange" :value="index" :range="array_loc">
						{{array_loc[index]}}&nbsp;&nbsp;&#xe602;
					</picker>
				</view>

			</view>


			<view class="linestyle">
				<view class="linestyle_left">
					数据类型:
				</view>
				<view class="linestyle_right">
					<radio-group class="" name="radio" style="size: 30upx; margin: 13upx 0upx;  ">
						<label v-for="(item,index) in radio_list_loc" :key="index" style="size: 30upx; margin: 4upx 10upx;  ">
							<radio :value="item" /> {{item}}
						</label>

					</radio-group>
				</view>
			</view>

			<view class="linestyle">
				<view class="linestyle_left">
					上传图片:
				</view>
				<view class="linestyle_right">

					<view class="image_input_box" v-if="!image" @tap="chooseImage">+</view>
					<image class="image_input_box" v-if="image" :src="image"></image>

				</view>
			</view>


		</view>





		<button formType="submit" class="tijiao_button_style">
			提交并保存
		</button>
	</form>

</template>
<script>
	import {
		mapState,
		mapMutations
	} from 'vuex'
	
	export default {
		data() {
			return {
				title: 'form',
				pickerHidden: true,
				chosen: '',
				array_loc: [],
				radio_list_loc: [],
				index: 0,
				image: '',
			}
		},
		computed: {
			...mapState(['hasLogin', 'userInfo', 'phonenum', 'openid'])
		
		},
		onLoad() {
			this.array_loc=this.big_field_list;
			this.radio_list_loc=this.radio_list;
		},
		methods: {
			bindPickerChange: function(e) {
				console.log('picker发送选择改变，携带值为', e.target.value)
				this.index = e.target.value
			},

			pickerConfirm: function(e) {
				this.pickerHidden = true
				this.chosen = e.target.value
			},
			pickerCancel: function(e) {
				this.pickerHidden = true
			},
			pickerShow: function(e) {
				this.pickerHidden = false
			},
			formSubmit: function(e) {
				// 				console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
				// 				console.log(this.array[e.detail.value.picker])
				if (e.detail.value.title) {
					// this.pushData_hot_card_list_beixuan_list(e.detail.value)
					this.upload_to_server({
						'form': 'hot_card_list_beifeng_list',
						'nickName': this.userInfo.nickName,
						'phonenum': this.phonenum,
						'openid': this.openid,
						'avatarUrl': this.userInfo.avatarUrl,
						'title': e.detail.value.title,
						'categorie': e.detail.value.radio,
						'big_field': this.big_field_list[e.detail.value.picker],
					});
					
					
					if(this.image!=''){this.uploadFile_image(e.detail.value.title,this.image)}
					
					uni.showLoading({
						title: '上传中'
					})
					setTimeout(function() {
						uni.hideLoading();
						uni.navigateBack({})
					}, 300);
				}
			},
			formReset: function(e) {
				console.log("清空数据")
				this.chosen = ''
			},



			chooseImage() {
				uni.chooseImage({
					count: 1,
					sourceType: ['album', 'camera'],
					sizeType: ['compressed', 'original'],
					success: (res) => {
						this.image = res.tempFilePaths[0];
					}
				})
			},
			



		}
	}
</script>

<style>
	.btn-submit {
		background-color: #007aff;
		color: #ffffff;
	}

	.image_input_box {
		margin: 14upx 10upx;
		width: 130upx;
		height: 130upx;
		border: 2upx solid #D9D9D9;
		color: #D9D9D9;
		font-size: 80upx;
		align-content: center;
		justify-content: center;

	}

	.border-style-blue {
		border: 2px solid blue;
		font-weight: normal;
		width: 125upx;
		padding: 0 10upx;
	}
</style>



